import axios from 'axios';

document.addEventListener("DOMContentLoaded", () => {
    //  header
    axios.get("components/header.html")
        .then(res => {
            document.getElementById("header").innerHTML = res.data;
            setTimeout(() => {
                if (typeof updateCartCount === 'function') updateCartCount();
                if (typeof updateLoginStatusUI === 'function') updateLoginStatusUI();
                if (typeof renderMailchimpForm === 'function') renderMailchimpForm();
            }, 0);
        })
        .catch(err => console.error(" header ", err));

    //  footer
    axios.get("components/footer.html")
        .then(res => {
            document.getElementById("footer").innerHTML = res.data;
        })
        .catch(err => console.error(" footer ", err));

    //  hash anchor 
    const hash = window.location.hash;
    if (hash) {
        setTimeout(() => {
            const target = document.querySelector(hash);
            if (target) {
                target.scrollIntoView({ behavior: "smooth" });
            }
        }, 100); // 
    }
});